.custom-select {
  display: block;
  height: 34px;
  position: relative;
  text-align: left;
  width: 100%;
}

.custom-select-placeholder,
.custom-select-value {
  position: relative;
}

.custom-select-input {
  border: none;
  bottom: 0;
  cursor: pointer;
  height: 100%;
  left: 0;
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  -webkit-appearance: none;
}

.custom-select-caret {
  margin-top: -1px;
  pointer-events: none;
  position: absolute;
  right: 12px;
  top: 50% !important;
}
